<template>
  <div class="container">
    <div class="dot"></div>
    <div class="pulse"></div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<style scoped lang="less">
@keyframes warn {
  0% {
    transform: scale(0.8);
    opacity: 0.7;
  }
  25% {
    transform: scale(0.85);
    opacity: 0.8;
  }
  50% {
    transform: scale(0.9);
    opacity: 1;
  }
  75% {
    transform: scale(1);
    opacity: 0.9;
  }
  100% {
    transform: scale(1.1);
    opacity: 0.8;
  }
}
@-webkit-keyframes "warn" {
  0% {
    -webkit-transform: scale(0.8);
    opacity: 0.7;
  }
  25% {
    -webkit-transform: scale(0.85);
    opacity: 0.8;
  }
  50% {
    -webkit-transform: scale(0.9);
    opacity: 1;
  }
  75% {
    -webkit-transform: scale(1);
    opacity: 0.9;
  }
  100% {
    -webkit-transform: scale(1.1);
    opacity: 0.8;
  }
}

.container {
  position: relative;
  width: 400px;
  height: 400px;
  border: 1px solid #000;
  // background: rgba(0, 0, 0, 0.5);
}
/* 保持大小不变的小圆圈  */
.dot {
  position: absolute;
  width: 60px;
  height: 60px;
  left: 15px;
  top: 15px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border: 2px solid red;
  border-radius: 20px;
  z-index: 2;
}
/* 产生动画（向外扩散变大）的圆圈  */
.pulse {
  position: absolute;
  background: url(../assets/home/level_gold@2x.png);
  // background: url("../assets/home/level_gold@2x.png");
  background-size: 100% 100%;
  width: 240px;
  height: 240px;
  left: 2px;
  top: 2px;
  // border: 6px solid red;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  border-radius: 30px;
  z-index: 1;
  opacity: 0;
  -webkit-animation: warn 3s ease-out;
  -moz-animation: warn 3s ease-out;
  animation: warn 3s ease-out;
  -webkit-animation-iteration-count: infinite;
  -moz-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}
</style>



